@import url(https://fonts.googleapis.com/css?family=Press+Start+2P&display=swap);
@font-face {
  font-family: 'runescape';
  font-style: normal;
  font-weight: normal;
  src:	url('https://images.wikia.nocookie.net/runescape/images/a/ab/Runescape_chat.eot') format('embedded-opentype'),
	local("RuneScape Chat"),
	url('https://images.wikia.nocookie.net/runescape/images/8/87/Runescape_chat.woff') format('woff');
}
 
@font-face {
  font-family: 'runescape-bold';
  font-style: normal;
  font-weight: bold;
  src:	url('https://images.wikia.nocookie.net/runescape/images/1/1e/Runescape_chat_bold.eot') format('embedded-opentype'),
	local("RuneScape Chat Bold 2"),
	local("RuneScape Chat Bold"),
	local("RuneScape Chat 2"),
	url('https://images.wikia.nocookie.net/runescape/images/e/e8/Runescape_chat_bold.woff') format('woff');
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.LoadingContainer{height:100vh;width:100vw;display:grid}.lds-roller{display:inline-block;position:relative;width:64px;height:64px;align-self:center;justify-self:center}.lds-roller div{-webkit-animation:lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;animation:lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;-webkit-transform-origin:32px 32px;transform-origin:32px 32px}.lds-roller div:after{content:" ";display:block;position:absolute;width:6px;height:6px;border-radius:50%;background:#8a3535;margin:-3px 0 0 -3px}.lds-roller div:nth-child(1){-webkit-animation-delay:-0.036s;animation-delay:-0.036s}.lds-roller div:nth-child(1):after{top:50px;left:50px}.lds-roller div:nth-child(2){-webkit-animation-delay:-0.072s;animation-delay:-0.072s}.lds-roller div:nth-child(2):after{top:54px;left:45px}.lds-roller div:nth-child(3){-webkit-animation-delay:-0.108s;animation-delay:-0.108s}.lds-roller div:nth-child(3):after{top:57px;left:39px}.lds-roller div:nth-child(4){-webkit-animation-delay:-0.144s;animation-delay:-0.144s}.lds-roller div:nth-child(4):after{top:58px;left:32px}.lds-roller div:nth-child(5){-webkit-animation-delay:-0.18s;animation-delay:-0.18s}.lds-roller div:nth-child(5):after{top:57px;left:25px}.lds-roller div:nth-child(6){-webkit-animation-delay:-0.216s;animation-delay:-0.216s}.lds-roller div:nth-child(6):after{top:54px;left:19px}.lds-roller div:nth-child(7){-webkit-animation-delay:-0.252s;animation-delay:-0.252s}.lds-roller div:nth-child(7):after{top:50px;left:14px}.lds-roller div:nth-child(8){-webkit-animation-delay:-0.288s;animation-delay:-0.288s}.lds-roller div:nth-child(8):after{top:45px;left:10px}@-webkit-keyframes lds-roller{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes lds-roller{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}.domain{stroke-opacity:0.3}.tick{stroke-opacity:0.3;shape-rendering:crispEdges}.tooltip{position:absolute;pointer-events:none;background:#820d0d;margin:24px 0px 0px 30px;padding-left:4px;padding-right:4px;font-size:12px;color:#ffffff;border-radius:2px;font-weight:bold;min-width:60px}svg{pointer-events:none}td{padding:8px !important;text-overflow:ellipsis;white-space:nowrap}html{background:#303030;overflow:hidden}.volume_bar{fill:#ae89d5}.PriceTableRow:hover{background:#474444 !important}.PriceTableRow.Selected{background:#212121 !important}.Wrapper{height:100vh;display:flex;flex-direction:column}.Container{display:grid;grid-template-columns:400px auto;height:100%}.Container.Expanded{grid-template-columns:auto}.Container .Sidebar{border-right:1px solid #515151;height:100%;display:grid;grid-template-areas:"header" "search" "items" "footer";grid-template-rows:auto auto 1fr auto}.Container .Sidebar .Header{padding-left:8px;padding-top:4px;padding-bottom:4px;grid-area:header;display:flex;background-color:#222}.Container .Sidebar .Header .Logo{text-align:center;font-size:20px;margin-left:12px;padding-top:4px;color:#e7d62b;border-radius:6px;font-family:runescape-bold}.Container .Sidebar .Footer{grid-area:footer;display:flex;flex-direction:row;width:100%}.Container .Sidebar .Footer>*{flex-grow:1;text-align:center;padding:4px;font-size:16px}.Container .Sidebar .SearchBarContainer{display:grid;grid-template-columns:1fr auto;padding:10px;box-sizing:border-box;grid-area:search}.Container .Sidebar .SearchBarContainer .Searchbar{height:100%}.Container .Sidebar .ItemsContainer{grid-area:items;overflow:hidden}.Container .Sidebar .SidebarItem{display:flex;flex-direction:row;color:#222;cursor:pointer}.Container .Sidebar .SidebarItem .SidebarItemImage{width:50px}.Container .Sidebar .SidebarItem.Active{background:black;color:white}.Container .Content{display:inline-flex;flex-direction:column;min-width:100%;overflow:hidden}.Container .Content.Expanded{display:none}.Container .Content .ChartContainer{flex-basis:0px;flex-grow:1;max-height:100%;margin:50px;position:relative}.Container .Content .ChartContainer .ChartTitle{display:flex;flex-direction:row;position:absolute;top:6px;margin-left:10px}.Container .Content .ChartContainer .Legend{position:absolute;pointer-events:none;margin:0px 0px 0px 20px;padding:5px 10px 5px 10px;background:#474444;background:rgba(20,20,20,0.7);min-width:100px;font-size:12px}.Container .Content .ChartContainer .Legend .Label{color:rgba(255,255,255,0.5)}.Container .Content .ChartContainer .Legend .Label.Pink{color:#d337ff}.Container .Content .ChartContainer .Legend .Label.Red{color:#d66061}.Container .Content .ChartContainer .Legend .Label.Purple{color:#c677dc}.Container .Content .ChartContainer .Legend .Label.Blue{color:#46a4f3}.Container .Content .ChartContainer .Legend .Label.Orange{color:goldenrod}.Container .Content .ChartContainer .Legend .Label.LightBlue{color:#6395a4}.Container .Content .ChartContainer .Legend .Label .Value{color:white}.Container .Content .ChartContainer .Legend .LegendHeader{display:flex}.Container .Content .ChartContainer .Legend .LegendHeader .LegendIcon{width:25px;height:25px}.Container .Content .ChartContainer .Legend .LegendHeader .ItemName{font-size:14px;margin-top:4px}.Container .Content .ItemInfo{display:grid;grid-template-areas:"pic name" "pic stats";grid-template-rows:auto 1fr;grid-template-columns:auto 1fr}.Container .Content .ItemInfo .LargeItemImage{width:250px;height:250px;grid-area:pic}.Container .Content .ItemInfo .LargeItemName{font-size:26px;grid-area:name;color:#222}.Container .Content .ItemInfo .Statistics{grid-area:stats;font-size:18px;color:#222}

